
  import { format } from 'date-fns'
  import {
    ComputedRef,
    PropType,
    computed,
    defineComponent,
    ref,
    watch,
    capitalize,
    onBeforeMount,
  } from 'vue'

  import StaticMap from '@/components/Common/StaticMap.vue'
  import NoWorkouts from '@/components/Workouts/NoWorkouts.vue'
  import { WORKOUTS_STORE } from '@/store/constants'
  import { ITranslatedSport } from '@/types/sports'
  import { IUserProfile } from '@/types/user'
  import { IWorkout } from '@/types/workouts'
  import { useStore } from '@/use/useStore'
  import { getDateWithTZ } from '@/utils/dates'

  export default defineComponent({
    name: 'WorkoutsList',
    components: {
      NoWorkouts,
      StaticMap,
    },
    props: {
      params: {
        type: Object as PropType<Record<string, string>>,
        required: true,
      },
      user: {
        type: Object as PropType<IUserProfile>,
        required: true,
      },
      sports: {
        type: Object as PropType<ITranslatedSport[]>,
      },
    },
    setup(props) {
      const store = useStore()
      const workouts: ComputedRef<IWorkout[]> = computed(
        () => store.getters[WORKOUTS_STORE.GETTERS.USER_WORKOUTS]
      )
      const per_page = 10
      const page = ref(1)
      const moreWorkoutsExist: ComputedRef<boolean> = computed(() =>
        workouts.value.length > 0
          ? workouts.value[workouts.value.length - 1].previous_workout !== null
          : false
      )

      onBeforeMount(() => {
        loadWorkouts()
      })

      function loadWorkouts() {
        page.value = 1
        store.dispatch(WORKOUTS_STORE.ACTIONS.GET_USER_WORKOUTS, {
          page: page.value,
          per_page,
          ...props.params,
        })
      }
      function loadMoreWorkouts() {
        page.value += 1
        store.dispatch(WORKOUTS_STORE.ACTIONS.GET_MORE_USER_WORKOUTS, {
          page: page.value,
          per_page,
          ...props.params,
        })
      }

      watch(
        () => props.params,
        async () => {
          loadWorkouts()
        }
      )

      return {
        moreWorkoutsExist,
        workouts,
        capitalize,
        format,
        getDateWithTZ,
        loadMoreWorkouts,
      }
    },
  })
